body {
  margin: 0;
  padding: 0;

  font-family: 'Montserrat', sans-serif;
  font-size: 14px;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: white;
  color: #141e55;

  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;

  padding-bottom: 10px;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, .5);

  float: left;

  text-transform: uppercase;
}

.Logo {
  height: 30px;
  margin-top: 10px;
}

.App-header-text {
  float:left;
  width: 100%;
  color: #001489;
}

.App-header h1 {
margin: 2px;
}

.App-header p {
  margin: 0px;
}  

ul {
  list-style: none;
  list-style-position: inside;
  padding-left: 0;
}


.App-content {
  margin-top: 85px;
}

footer {
  color: gray;
  font-size: small;
  margin-bottom: 10px;
}
/* .DeviceList {
    text-align: center;
} */

.DeviceItem {
    width: 80%;
    border-bottom: 1px solid black;
    padding: 10px;
    text-align: left;
    margin: auto;
}

.PlatformIcon {
    float: left;
    padding-right: 10px;    
  /* width: 10%; */
}


.DeviceInfo {
    float: left;
    margin: auto;
  
  /* width: 90%; */
}

.DeviceInfo p {
    margin: 0px;
}

.DeviceItem:after {
    content: "";
    display: table;
    clear: both;
  }

.OSStatistics {
    text-align: center;
}

.PieChart {
    width: 30%;
    margin: auto;
}

.Table { 
    margin: 0px auto; 
    margin-top: 10px;
}

table, th, td {
    border: 1px solid white;
    border-collapse: collapse;
  }

  th, td {
    padding: 15px;
  }
